import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { Helmet } from 'react-helmet'
import './Login.scss'

const Login = ({ setIsLogined }) => {
  const navigate = useNavigate()
  const [id, setId] = useState('')
  const handleSubmit = () => {
    setIsLogined(true)
    navigate(`/${id}/posts`)
  }

  return (
    <div className="login">
      <Helmet>
        <title>{'Task_3 | '}</title>
      </Helmet>
      <h3 className="login__heading"></h3>
      <form onSubmit={handleSubmit} className="login__form">
        <label></label>
        <input type="email" required className="login__mail" />
        <label></label>
        <input type="password" required className="login__mail" />
        <label>ID </label>
        <input
          type="number"
          min="1"
          max="10"
          required
          className="login__mail"
          value={id}
          onChange={(event) => setId(event.target.value)}
        />
        <button type="submit" className="login__buttonLog">
          
        </button>
      </form>
      <p>
            {' '}
        <a href="https://jsonplaceholder.typicode.com/" className="login__link">
          jsonplaceholder.typicode.com
        </a>
        <br />
           , , .
        <br />
        <strong></strong> -  Email;
        <br />
        <strong></strong> -   ;
        <br />
        <strong>ID </strong> -     (
         0  10).
      </p>
    </div>
  )
}

export default Login
